const SUPPLYPATOPTIONS={
	'type':[
		SELECTOPTION.FIRST,
		{ value: '_PATT1', text: '' },
		{ value: '_PATT2', text: '' },
		{ value: '_PATT3', text: '' },
		{ value: '_PATT4', text: '' },
		{ value: '_PATT5', text: '' },
		{ value: '_PATT6', text: '' },
		SELECTOPTION.LAST
	]
}

const SUPPLYPATFIELDS=[
		FIELDSDEF.SN,
		//input
		{isInDetail:false,key:'topic',label:{en:'Topic',chn:''},
			tdClass:'text-left font-weight-bold align-middle',
			/* tdAttr:(value, key, item)=>{
				
				console.log(value)
				console.log(key)
				console.log(item)
				
				return {zz:'font-weight-bold'}
			}, */
			formElement:{name:'input',type:'text'}
		},
		{isInDetail:false,key:'patowner',label:{en:'Owner',chn:''},formElement:{name:'input',type:'text'}},
		{isInDetail:true,key:'inventor',label:{en:'Inventor',chn:''},formElement:{name:'input',type:'text'}},
		{isInDetail:true,key:'author',label:{en:'Author',chn:''},formElement:{name:'input',type:'text'}},
		//select
		{isInDetail:false,key:'type',label:{en:'Type',chn:''},
			//BSVcallback,isInDetail:false
			//formatter:(value, key, item) =>{
			formatter:value =>{
				let defArr=SUPPLYPATOPTIONS['type']
				
				defArr.forEach(obj=>{
					value=obj.value==value?obj.text:value
					
				})
				
				//console.log(value, key, item) 
				
				return value
			},
			formElement:{
				name:'select',
				type:'',
				options:SUPPLYPATOPTIONS['type']
			}
		},
		//none
		{isInDetail:false,key:'id',label:{en:'Id',chn:''},formElement:''},
		{isInDetail:true,key:'patnum',label:{en:'Patent No.',chn:''},formElement:''},
		FIELDSDEF.ACTIONS,
			//{key:'',isInDetail:false,label:{}},
]
	
	
